//자주쓰는 속성들은 잘라넣자
@import './reset.scss';

// $변수명 : 변수에 넣을 값;
* { margin: 0px; padding:0px; box-sizing: border-box;}

.myAlert {
    background: #eeeeee;
    padding: 20px;
    border-radius: 5px;
    width: 100%;
    max-width: 550px;
    margin: 10px auto;
    p {
        margin-bottom: 0px;
    }
}

.myAlert-pink {
    @extend .myAlert; //myAlert의 모든 css문법을 복사해!
    background:rgb(255, 241, 244);
}

