.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.jumbo {
  background-image: url("./20200702120918093_8809721501856_ZW_00.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  padding: 50px;
  width: 640px;
  margin: 0 auto;
}
.jumbo > h3 { 
  font-size: 3.5vw;
}
.jumbo > Button {
  margin-top: 20px;
}

.container {
  width: 640px;
}

.row {
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media all and (max-width:640px) {
  .jumbo {
    width: 100%;
  }
  .container {
    width: 100%;
  }
}